
import Auth from "../../services/routes";
import * as moment from "moment";
export default {
    name: "login",
    components: {},
    methods: {
        goTO(url) {
            this.$router.push(url);
        },
        userLogin() {
            const data = {
                EMAIL: this.dataLogin.EMAIL,
                PASSWORD: this.dataLogin.PASSWORD,
            };

            if (this.dataLogin.EMAIL === "") {
                this.validEmail = true;
            } else {
                this.validEmail = false;
            }
            if (this.dataLogin.PASSWORD === "") {
                this.validPass = true;
            } else {
                this.validPass = false;
            }

            if (this.validPass === false && this.validEmail === false) {
                this.load = true;

                Auth.login(data)
                    .then((r) => {
                        localStorage.setItem('last', moment().toDate());
                        console.log(r.data)
                        this.UserLogin = r.data;
                        this.$notify.success({
                            title: "Sucesso",
                            message: "Seja Bem Vinda Val! " ,
                            offset: 120,
                            duration: 3000,
                        });
                        this.$router.push("/dashboard");
                    })
                    .catch((e) => {
                          console.log(e)
                        this.$message({
                            showClose: true,
                            type: "error",
                            message: "Email ou senha inv√°lido",
                            offset: 0,
                        });
                        this.load = false;
                    });
            }
        },
    },
    data() {
        return {
            dataLogin: {
                EMAIL: "",
                PASSWORD: "",
                subscribe: true,
            },
            UserLogin: null,
            load: false,
            alertSuccess: false,
            alertFail: false,
            message: "",
            validEmail: false,
            validPass: false,
            internalClientOn: false,
            internalClientSet: false,
            dataUser: null,
        };
    },
    mounted() { },
};
